import { array } from 'vega-util';
import { isConditionalDef, isConditionalParameter } from '../../../channeldef';
import { expression } from '../../predicate';
import { parseSelectionPredicate } from '../../selection/parse';
/**
 * Return a mixin that includes a Vega production rule for a Vega-Lite conditional channel definition
 * or a simple mixin if channel def has no condition.
 */

export function wrapCondition(model, channelDef, vgChannel, refFn) {
  var condition = isConditionalDef(channelDef) && channelDef.condition;
  var valueRef = refFn(channelDef);

  if (condition) {
    var conditions = array(condition);
    var vgConditions = conditions.map(c => {
      var conditionValueRef = refFn(c);

      if (isConditionalParameter(c)) {
        var {
          param,
          empty
        } = c;
        var test = parseSelectionPredicate(model, {
          param,
          empty
        });
        return Object.assign({
          test
        }, conditionValueRef);
      } else {
        var _test = expression(model, c.test); // FIXME: remove casting once TS is no longer dumb about it


        return Object.assign({
          test: _test
        }, conditionValueRef);
      }
    });
    return {
      [vgChannel]: [...vgConditions, ...(valueRef !== undefined ? [valueRef] : [])]
    };
  } else {
    return valueRef !== undefined ? {
      [vgChannel]: valueRef
    } : {};
  }
}